.catalog {
  min-width: 800px;
  padding-bottom: 72px;
  display: grid;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
  position: relative;
  box-shadow: 1px 2px 4px #0000000d;
}

.logo__image {
  justify-content: center;
  display: flex;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  align-items: center;
  list-style-type: none;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.cards {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .cards {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 198px;
  height: 406px;
}

.card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-repeat: no-repeat;
  background-size: 160px 134px;
  width: 160px;
  height: 134px;
  margin: 32px auto;
}

.card__title {
  color: #060b35;
  width: 166px;
  margin-top: 40px;
  margin-left: 16px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  margin-left: 16px;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: flex-end;
  width: 166px;
  margin-top: 24px;
  margin-left: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-button {
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  margin-left: 16px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
}

.card__buy-button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.stars {
  justify-content: space-between;
  align-items: flex-end;
  width: 166px;
  margin: 16px auto;
  display: flex;
}

.stars__all-stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__reviews {
  color: #060b35;
  text-align: right;
  flex-direction: column;
  justify-content: flex-end;
  width: 53px;
  height: 16px;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

